import React from "react";
import { TaskState } from "../../Store/tasks";
import { Dialog } from "../Utility/Dialog/Dialog";
import { Button } from "../Utility/Button/Button";

export type TaskStateDisplayState = {
  state: TaskState;
  done?: boolean;
  readonly?: boolean;
  stateChanged: (value: TaskState | undefined, done: boolean | undefined) => void;
};

function getLabel(state: TaskState, done: boolean | undefined): string {
  switch (state) {
    case "created": return "En attente";
    case "in-backlog": return "Dans le radar";
    case "active": return "En cours";
    case "closed": return done ? "Terminée" : "Abandonnée";
  }
}

export const TaskStateDisplay: React.FunctionComponent<TaskStateDisplayState> = ({ state, done, readonly, stateChanged }) => {
  const [isDialogOpen, setIsDialogOpen] = React.useState(false);
  const className = state === "closed" ? `${state}-${done}` : state;

  const stateClicked = () => {
    if (readonly) {
      setIsDialogOpen(false);
      stateChanged(state, done);
    } else {
      setIsDialogOpen(true);
    }
  }

  const newStateClicked = (newState: TaskState, newDone: boolean | undefined) => {
    setIsDialogOpen(false);
    stateChanged(newState, newDone);
  }

  const renderButton = (newState: TaskState, newDone?: boolean) => {
    if (state === newState && done === newDone) {
      return null;
    }
    return <TaskStateDisplay key={newState} state={newState} done={newDone} readonly={true} stateChanged={() => newStateClicked(newState, newDone)} />;
  }

  return <div>
    <div className={`task-state ${className}`} onClick={() => stateClicked()}>{getLabel(state, done)}</div>
    {
      isDialogOpen &&
      <Dialog buttons={[{ text: "Annuler", onclick: () => { setIsDialogOpen(false); stateChanged(undefined, undefined); } }]}>
        {renderButton("created")}
        {renderButton("in-backlog")}
        {renderButton("active")}
        <div className="task-state-closed">
          {renderButton("closed", true)}
          {renderButton("closed", false)}
        </div>
      </Dialog>}
  </div >;
}
