import React from "react";
import "./Tasks.css";
import { TaskComponent } from "./TaskComponent";
import { TaskForm } from "./TaskForm";
import { useTasks } from "../../Store/tasks";
import { Button } from "../Utility/Button/Button";

type TaskFormState = {
  isOpen: boolean;
  taskId?: string;
};

export function TaskList() {
  const tasks = useTasks();
  const [taskForm, setTaskForm] = React.useState<TaskFormState>({ isOpen: false });

  /*React.useEffect(() => {
    (async () => {
      try {
        setData(await getData());
      } catch (error) {
        setData(error.toString());
      }
    })();
  }, []);*/

  return <div className="tasks">
    {Object.keys(tasks).map(id => <TaskComponent key={id} task={tasks[id]} onClick={() => setTaskForm({ isOpen: true, taskId: id })} />)}
    {
      taskForm.isOpen &&
      <TaskForm
        task={taskForm.taskId ? tasks[taskForm.taskId] : undefined}
        onClose={() => setTaskForm({ isOpen: false })}
      />}
    <Button isWide onClick={() => setTaskForm({ isOpen: true, taskId: undefined })}>+</Button>
  </div>;
}
